@import "./base/base";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --font-primary: 'Inter';
  --bg-color: #{$gray-color-0};
  /* set base colors */
  --color-text: #{$gray-color-900};

  /* set base values */
  --text-base-size: 1em;
  --text-scale-ratio: 1.2;

  /* type scale */
  --text-xs: calc(1em / (var(--text-scale-ratio) * var(--text-scale-ratio)));
  --text-sm: calc(1em / var(--text-scale-ratio));
  --text-md: calc(1em * var(--text-scale-ratio));
  --text-lg: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xxl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));
  --text-xxxl: calc(1em * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio) * var(--text-scale-ratio));

  /* set line-height */
  --heading-line-height: 1.25;
  --body-line-height: 1.5;


  /* set color */

  --primary-color: #{$primary-color};
  --secondary-color: #{$secondary-color};

  --acent-color: #{$accent-color};

  --dark-color: #{$dark-color};
  --light-color: #{$light-color};

  --gray-color-900: #{$gray-color-900};
  --gray-color-800: #{$gray-color-800};
  --gray-color-700: #{$gray-color-700};
  --gray-color-600: #{$gray-color-600};
  --gray-color-500: #{$gray-color-500};
  --gray-color-400: #{$gray-color-400};
  --gray-color-300: #{$gray-color-300};
  --gray-color-200: #{$gray-color-200};
  --gray-color-100: #{$gray-color-100};
  --gray-color-0: #{$gray-color-0};


  --feedback-critical-light: #FA0A32;
  --feedback-critical: #FFC2C6;

  --feedback-warning-light: #FCDA6A;
  --feedback-warning: #FC9700;
  
  --feedback-success-light: #389E0D;
  --feedback-success: #A7EB92;



  --container: #{$xlarge};
  --container-tablet-landscape: #{$large};
  --container-tablet-portrait: #{$medium};
  --container-phone: #{$small};
  

}

body {
  font-family: var(--font-primary);
  background-color: var(--bg-color);
}

h1,
h2,
h3,
h4,
form legend {
  margin-bottom: var(--space-xxs);
  line-height: var(--heading-line-height);
  color: var(--color-text);
}

h1,
h2,
p{
  font-weight: 400;

}
b,
strong {
  font-weight: 700;
}

/* text size */
.text--xxxl {
  font-size: var(--text-xxxl);
}

h1,
.text--xxl {
  font-size: var(--text-xxl);
}

h2,
.text--xl {
  font-size: var(--text-xl);
}

h3,
.text--lg {
  font-size: var(--text-lg);
}

h4,
.text--md {
  font-size: var(--text-md);
}

.text--sm,
small {
  font-size: var(--text-sm);
}

.text--xs {
  font-size: var(--text-xs);
}

p {
  line-height: var(--body-line-height);
  color: var(--gray-color-700);
}

ul li{
  color: var(--gray-color-700);
}

.mt-2{
  margin-top: #{$size*2};
}
.mb-2{
  margin-bottom: #{$size*2};
}
.mt-3{
  margin-top: #{$size*3};
}
.mb-3{
  margin-bottom: #{$size*3};
}
.mt-4{
  margin-top: #{$size*4};
}
.mb-4{
  margin-bottom: #{$size*4};
}
.mt-2{
  margin-top: #{$size*2}!important;
}
.mt-3{
  margin-top: #{$size*3}!important;
}
.mt-4{
  margin-top: #{$size*4}!important;
}
.mt-5{
  margin-top: #{$size*5}!important;
}
.mb-5{
  margin-bottom: #{$size*5};
}
.ml-5{
  margin-left: #{$size*5};
}
.mr-5{
  margin-right: #{$size*5};
}
.pt-2{
  padding-top: #{$size*2};
}
.pt-5{
  padding-top: #{$size*5};
}

.row{
  display: flex;
  flex-direction: row;
}
hr {
  display: block;
  unicode-bidi: isolate;
  margin-block-start: 0.5em;
  margin-block-end: 0.5em;
  margin-inline-start: 0;
  margin-inline-end: 0;
  overflow: hidden;
  border-style: inset;
  border-width: 1px;
  border: 1px solid var(--gray-color-200);
}
.text-center{
  text-align: center!important;
}
.align-center{
  display: flex!important;
  align-items: center!important;
}



.FormAction{
  width: 60%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: left;
  a{
      color: var(--color-text);
      text-decoration: none;
  }
  hr{
      border: 1px solid #e8e8e8;
      width: 50%;
      margin-bottom: 2rem;
  }

}